import React, { useEffect, useState } from 'react';

import Description from '../Description/Description';
import FictionCard from '../FictionCard/FictionCard';

import styles from './Fiction.module.scss';

function Fiction() {
    const [stories, setStories] = useState([]);
    let numberOfStories = 0;
    let readyCards = 0;

    function onFictionCardReady() {
        readyCards++;

        if (readyCards === numberOfStories) {
            const hash = window.localStorage.getItem('hash');

            if (hash) {
                window.location.hash = hash;
                window.localStorage.removeItem('hash');
            }
        }
    }

    useEffect(() => {
        fetch('exclusive-fiction.json')
            .then(response => {
                return response.json();
            })
            .then(data => {
                numberOfStories = data.length;

                setStories(data.map((dataPoint, index) => {
                    return <FictionCard key={index} {...dataPoint} onFictionCardReady={onFictionCardReady} />
                }));
            });
    }, []);

    return (
        <React.Fragment>
            <Description>
                <p>In this section you can find exclusive short fiction and poetry published only here. <strong>More coming soon...</strong></p>
            </Description>
            <div className={styles['fiction-container']}>{stories}</div>
        </React.Fragment>
    );
}

export default Fiction;