import React, { useEffect, useState } from 'react';

import Description from './../Description/Description';
import LitCard from './../LitCard/LitCard';

import styles from './Home.module.scss';

function Home() {
    const [cards, setCards] = useState([]);

    useEffect(() => {
        fetch('data.json')
            .then(response => {
                return response.json();
            })
            .then(data => {
                setCards(data.map((dataPoint, index) => {
                    return <LitCard key={index} {...dataPoint} />
                }));
            });
    }, []);

    return (
        <React.Fragment>
            <Description capitalLetter>
                <p>Welcome to my personal website. My name is Dimitar Dakovski and I am a software engineer who lives in Sofia, Bulgaria with my wife, son, and two cats. I am enthusiastic about all things fantasy and science fiction, and, on this page, you can find all my published writings. I hope you have the opportunity to read at least some of them.</p>
                <p>This web page is also built entirely by me using the technologies <a href="https://reactjs.org/" target="_blank" rel="noreferrer">React</a>, <a href="https://sass-lang.com/" target="_blank" rel="noreferrer">Sass</a>, and the Material Design themes <a href="https://material.io/design/material-studies/basil.html" target="_blank" rel="noreferrer">Basil</a> and <a href="https://material.io/design/material-studies/reply.html" target="_blank" rel="noreferrer">Reply</a>.</p>
            </Description>
            <div className={styles['card-container']}>{cards}</div>
        </React.Fragment>
    );
}

export default Home;