import React, { useState } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink
} from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faReact, faSass } from '@fortawesome/free-brands-svg-icons';
import { faHome, faRocket, faUser } from '@fortawesome/free-solid-svg-icons';
import Home from './../Home/Home';
import Fiction from './../Fiction/Fiction';
import Contact from './../Contact/Contact';

import styles from './App.module.scss';

export const ThemeContext = React.createContext();

function App() {
  const [theme, setTheme] = useState(localStorage.getItem('theme') || 'basil');

  function toggleTheme() {
    setTheme(prevTheme => {
      const newTheme = prevTheme === 'basil' ? 'reply' : 'basil';

      localStorage.setItem('theme', newTheme);
      return newTheme;
    });
  }

  return (
    <Router>
      <ThemeContext.Provider value={theme}>
        <div className={styles['App'] + ' ' + styles[theme]}>
          <div className={styles['title']}>Dimitar Dakovski Writes
          <div className={styles['theme-button']} onClick={toggleTheme} title="Change theme"></div>
          </div>
          <nav className={styles['nav']}>
            <NavLink to="/" activeClassName={styles['active']} exact title="Home"><FontAwesomeIcon icon={faHome} /></NavLink>
            <NavLink to="/fiction" activeClassName={styles['active']} title="Exclusive Fiction"><FontAwesomeIcon icon={faRocket} /></NavLink>
            <NavLink to="/contact" activeClassName={styles['active']} title="Contact"><FontAwesomeIcon icon={faUser} /></NavLink>
          </nav>
          <div className={styles['main']}>
            <Switch>
              <Route path="/fiction">
                <Fiction />
              </Route>
              <Route path="/contact">
                <Contact />
              </Route>
              <Route path="/">
                <Home />
              </Route>
            </Switch>
          </div>
          <div className={styles['footer']}>
            <div>Â© {new Date().getFullYear()} by Dimitar Dakovski</div>
            <div>Powered by <FontAwesomeIcon icon={faReact} /> and <FontAwesomeIcon icon={faSass} /></div>
          </div>
        </div>
      </ThemeContext.Provider>
    </Router>
  );
}

export default App;
