import React, { useContext, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';

import { ThemeContext } from './../App/App';

import styles from './FictionCard.module.scss';

export default function FictionCard({ title, categories, description, image, datePublished, language, GRLink, text, size, onFictionCardReady }) {
    const theme = useContext(ThemeContext);
    const id = title.toLowerCase().replace(/ /g, '-').replace(/'/g, '');
    const classes = [styles['fiction-card'], styles[theme], styles[size]];
    const categoriesOutput = [];
    const withDescription = description || language || GRLink;

    if (withDescription) {
        classes.push(styles['with-description']);
    }

    if (categories[0] === 'Poetry') {
        classes.push(styles['poetry']);
    }

    for (let i = 0; i < categories.length; i++) {
        categoriesOutput.push(' ' + categories[i] + ' ');

        if (i < categories.length - 1) {
            categoriesOutput.push(<FontAwesomeIcon key={i} icon={faArrowRight} />);
        }
    }

    if (datePublished instanceof Date === false) {
        datePublished = new Date(datePublished);
    }

    useEffect(() => {
        onFictionCardReady();
    }, []);

    return (
        <div id={id} className={classes.join(' ')}>
            <div className={styles['cover']} style={{ backgroundImage: `url(${image})` }}></div>
            <h3 className={styles['title']}>{title}</h3>
            <div className={styles['date-published']}>Posted on: {datePublished.toLocaleDateString()}</div>
            <div className={styles['categories']}>{categoriesOutput}</div>
            {withDescription
                ? <div className={styles['details']}>
                    {description ? <ReactMarkdown>{description}</ReactMarkdown> : null}
                    {language ? <div><strong>Language:</strong> {language}</div> : null}
                    {GRLink ? <a href={GRLink} target="_blank" rel="noreferrer" className={styles['good-reads-icon']} title="Goodreads page">g</a> : null}
                </div>
                : null}
            <div className={styles['text']}><ReactMarkdown allowDangerousHtml>{text.replace(/\n/g, '<br />')}</ReactMarkdown></div>
        </div>
    )
}